import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { deleteBookTrials, getAllDeletedBookTrials } from '../../../../../store/actions/SubscriptionActions';
import { withStyles } from '@material-ui/styles';
import {
    Grid,
    IconButton,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    TablePagination,
    OutlinedInput,
    InputAdornment,
    Tooltip
} from '@material-ui/core';
import { IconSearch } from '@tabler/icons';
import SubjectIcon from '@material-ui/icons/Subject';
import ArrowForwardIcon from '@material-ui/icons/ArrowUpward';
import useStyles from '../../../../ViewTableStyles';
import useUserRole from '../../../../../hooks/useUserRole';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import MainCard from '../../../../../ui-component/cards/MainCard';
const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);
export default function DeletedBookTrials() {
    const classes = useStyles();
    useUserRole({ roleList: ['ADMIN', 'SCHOOL_ADMIN', 'SUPER_ADMIN'] });
    const { loggedUserProfile } = useSelector((state) => state?.userProfile);
    const dispatch = useDispatch();
    const [value, setValue] = useState('');
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(25);

    const { allBookTrials } = useSelector((state) => state?.subscriptions);
    const newData = allBookTrials?.content;

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
    };
    const handleDeleteBookTrial = (id) => {
        const payload = {
            deleted: false
        };
        dispatch(
            deleteBookTrials(payload, id, () => {
                dispatch(getAllDeletedBookTrials(value ? value : '', page + 1, rowsPerPage));
            })
        );
    };

    useEffect(() => {
        dispatch(getAllDeletedBookTrials(value ? value : '', page + 1, rowsPerPage));
    }, [rowsPerPage, page, value]);

    return (
        <>
            <MainCard>
                <div className={classes.titleContainer}>
                    <SubjectIcon className={classes.viewIcon} />
                    <span className={classes.pageTitle}>&nbsp; Deleted Book Trials </span>
                </div>
                <Paper elevation={5} className={classes.root}>
                    <Grid container spacing={3} style={{ display: 'flex', justifyContent: 'space-between' }}>
                        <Grid item xs={12} sm={6} md={6}>
                            <div>
                                <OutlinedInput
                                    className={classes.searchControl}
                                    id="input-search-header"
                                    value={value}
                                    onChange={(e) => {
                                        setValue(e?.target?.value);
                                        setPage(0);
                                    }}
                                    placeholder="Search"
                                    startAdornment={
                                        <InputAdornment position="start">
                                            <IconSearch stroke={1.5} size="1rem" className={classes.startAdornment} />
                                        </InputAdornment>
                                    }
                                />
                            </div>
                        </Grid>
                    </Grid>
                    <TableContainer className={classes.tableContainer}>
                        <Table className={classes.table} aria-label="simple table">
                            <TableHead className={classes.tableHeaderCell}>
                                <StyledTableRow>
                                    <TableCell> Name </TableCell>
                                    <TableCell> Age </TableCell>
                                    <TableCell align="center"> Contact </TableCell>
                                    <TableCell align="center"> Comment </TableCell>
                                    {loggedUserProfile?.role === 'SUPER_ADMIN' && <TableCell align="center"> Action </TableCell>}
                                </StyledTableRow>
                            </TableHead>
                            {!newData ? (
                                <caption style={{ textAlign: 'center' }}>Sorry! No data available</caption>
                            ) : (
                                <TableBody>
                                    {newData?.map((row, index) => {
                                        return (
                                            <StyledTableRow key={row?.id}>
                                                <TableCell>{row?.name}</TableCell>
                                                <TableCell>{row?.age}</TableCell>
                                                <TableCell align="center">{row?.contact}</TableCell>
                                                <TableCell align="center">{row?.comment}</TableCell>
                                                {loggedUserProfile?.role === 'SUPER_ADMIN' && (
                                                    <TableCell align="center">
                                                        <Tooltip title="Restore Book Trial">
                                                            <IconButton
                                                                onClick={() => {
                                                                    handleDeleteBookTrial(row.id);
                                                                }}
                                                            >
                                                                <ArrowForwardIcon color="primary" style={{ cursor: 'pointer' }} />
                                                            </IconButton>
                                                        </Tooltip>
                                                    </TableCell>
                                                )}
                                            </StyledTableRow>
                                        );
                                    })}
                                </TableBody>
                            )}
                        </Table>
                    </TableContainer>
                    <TablePagination
                        rowsPerPageOptions={[25, 50, 100]}
                        component="div"
                        count={allBookTrials?.totalElements || 0}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                        sx={{
                            '.MuiTablePagination-displayedRows': {
                                marginBottom: 0
                            },
                            '.MuiTablePagination-selectLabel': {
                                marginBottom: 0
                            }
                        }}
                    />
                </Paper>
            </MainCard>
        </>
    );
}
